import Styles from './Feedback.module.css';

function Feedback() {
    return (
        <section className={Styles.feedback}>
            <div className={Styles.feedback__textBlock}>
                <h2 className={Styles.feedback__title}>    </h2>
                <p className={Styles.feedback__text}> ,       30 ,      .</p>
            </div>
            <form className={Styles.form}>
                <h3 className={Styles.form__title}>  </h3>
                <ul className={Styles.form__inputList}>
                    <input className={Styles.form__input} placeholder=' '></input>
                    <input className={Styles.form__input} placeholder='  '></input>
                    <input className={Styles.form__input} placeholder=' '></input>
                </ul>
                <button className={Styles.form__button}> </button>
                <p className={Styles.form__subtext}> ,   <br />   </p>
            </form>
        </section>
    )
}

export default Feedback;